actions. Thus far, Rhode Island has faced challenges in effectively and equitably targeting communities facing the highest risk of opioid overdose events before an outbreak. The state has historically analyzed trends at the local municipality level but needs a highly spatial and temporal resolution of risk across the state for prioritizing interventions in the vulnerable neighborhoods. Though various interventions have been deployed in the field, for example, 25,742 naloxone kits have been distributed by the end of 2020, the increasing trend of opioid overdose deaths continues. ")]),_c('h4',[_vm._v("Solution")]),_vm._m(4),_vm._m(5),_c('p',[_vm._v(" As a subset of PROVIDENT, our goal is to predict opioid overdose risk in Rhode Island by CBG on a six-month rolling basis. We did a comparative analysis of ensemble, gaussian processes, and deep learning models evaluating the accuracy, interpretability, and computational effectiveness in predicting the rank of each CBG’s risk of overdoses. ")]),_c('h4',[_vm._v("Evaluation Criteria")]),_c('p',[_vm._v(" For intervention distribution fairness across localities and consistent performance comparison, all models are compared using their capture rate under the 20% lightly constrained (LC 20) scenario: 20% of the total CBGs with the highest predicted risks, at least one CBG per town. ")]),_c('h4',[_vm._v("Data")]),_c('p',[_vm._v(" Our main data sources include: ")]),_vm._m(6),_c('p',[_vm._v(" Through literature review and expert interviews, we were able to find some of the key indicators. We further narrowed down our feature set by using Recursive Feature Elimination and Random Forest feature importances, as well as performed dimensionality reduction using Linear/Kernel Principal Component Analysis. ")]),_c('h4',[_vm._v("Models")]),_c('strong',[_vm._v("Gaussian Process")]),_c('p',[_vm._v(" The Gaussian Process (GP) method has good prospects for spatio-temporal prediction by multiplying kernels. The best GP model uses features selected by Recursive Feature Elimination, distance-weighted spatial aggregates of those features, and each Census Block Group’s centroid coordinates, in total 140 features. It captures 40.5% drug overdose deaths in the period of 2020.1 (see Evaluation Criteria for information). ")]),_c('strong',[_vm._v("Gradient Boost")]),_c('p',[_vm._v(" The XGBoost (XGB) method is highly efficient and accurate because of parallel tree boosting. The best XGB model incorporates 16 principal components extracted from an original set of 143 features, using an 8-degree poly kernel. It captures 40.1% drug overdose deaths in the period of 2020.1 (see Evaluation Criteria for detailed information). ")]),_c('strong',[_vm._v("Random Forest")]),_c('p',[_vm._v(" The Random Forest (RF) method has good accuracy and some interoperability to help characterize the model’s logic via feature importance's. The best RF model uses 25 top important features from the previous two periods. It on average captures 40.2% drug overdose deaths in the periods of 2019.2 and 2020.1 (see Evaluation Criteria for detailed information). ")]),_c('strong',[_vm._v("Graph Convolutional Network")]),_c('p',[_vm._v(" The Graph Convolutional Network (GCN) method is good at capturing spatio-temporal relationships when a large amount of data and steps are available. The best GCN model uses features of the previous period, plus distance-weighted spatial aggregates of those features, in total 291 features. It captures 37.4% of drug overdose deaths in the period of 2020.1 (see Evaluation Criteria for detailed information). ")]),_c('h4',[_vm._v("Results and Implications")]),_c('p',[_vm._v(" GP, XBG, and RF all show promise in predicting OOEs per CBG. Given the similar LC20 performance of GP, XGB, and RF, we are unable to recommend a single model for use in the trial. We believe given more observations, a single model may distinguish itself and we recommend the study continue to test these models with the release of more data. ")]),_c('p',[_vm._v(" The PROVIDENT team will continue the modeling effort in the next few months with a few more available periods of data to finalize the model selection by late September 2021 for the randomized control trial. This information will be distributed to RIDOH and community organizations to deploy targeted interventions to prevent overdoses. Community organizations will have the power to choose what type of intervention, such as street outreach and educational workshops, is suitable in local neighborhoods. ")]),_c('h4',[_vm._v(" Acknowledgements ")]),_c('p',[_vm._v(" We would like to thank our project sponsors Dr. Daniel Neill (New York University), Dr. Magdalena Cerda (New York University Langone Health), and Bennett Allen (New York University Langone Health) for their guidance and collaboration on this project. We would also like to thank the following for their expertise and assistance: Dr. Brandon Marshall (Brown University), Dr. Will Goedel (Brown University), Claire Pratty (Brown University), Maxwell Krieger (Brown University), Konstantin Klemmer (University of Warwick), Abigail Cartus (Brown University), Rhode Island Department of Health, and NYU Center for Urban Science and Progress. ")])]):_vm._e()]),_c('div',{staticClass:"footer text-center "},[_c('hr'),_c('div',{staticClass:"d-flex flex-column footer-panel"},[_c('div',{staticClass:"d-flex justify-content-around"},[_vm._m(7),_c('div',{staticClass:"landing-logo mb-4"},[_c('a',{attrs:{"href":"https://cusp.nyu.edu/","target":"_blank"}},[_c('img',{staticStyle:{"height":"100%","width":"auto"},attrs:{"src":_vm.nyu_logo,"alt":""}})])])]),_vm._m(8)])])]),_vm._m(9),_c('div',{attrs:{"id":"map-container"}},[_c('MglMap',{attrs:{"accessToken":_vm.accessToken,"mapStyle":_vm.mapStyle,"container":"map-parent","zoom":_vm.zoom,"center":_vm.center},on:{"update:mapStyle":function($event){_vm.mapStyle=$event},"update:map-style":function($event){_vm.mapStyle=$event},"load":_vm.onMapLoaded}},[_c('MglNavigationControl',{at